@import 'layout';
@import 'components';

body{
    background-color: #C7A98A;
}
.section1{
    height: 100vh;
    overflow: hidden;
    position: relative;
    }
    .imginicio{
        width: 100%;
    
    }
    .textoinicio{
        background-color: #C7A98A;
        border: solid 2px #C7A98A;    
        font-size: 300%;
        font-weight: 100;
        padding: 2%;
        position: absolute;
        right: 32%;
        top: 60%;
        
    }
    .textosubmenu{
        color: black;
        font-size: 300%;
        font-weight: 100;
        text-align: center;
    }
    
    .ubi{
        text-decoration: overline;
        
    }
    
    .ubi:hover{
        text-decoration: none;
    }
    
    .tab{
        display: flex;
        justify-content: space-around;
    }
    
    .section2 {
        
        div{
        display: flex;
        flex-wrap: wrap;
        font-size: large;
        justify-content: space-around;
        }
    
        #city1{
            left: 0%;
            position: absolute;
            top: 155%;
            transform: rotate(-90deg);
        }
        #city2{
            position: absolute;
            right: 0%;
            top: 155%;
            transform: rotate(90deg);
    
        }
        .gifs img{
            height: 600px;
            padding-bottom: 4%;
            width: 600px;
            }
    }